.block-title {
	display: flex;
	align-items: center;
	width: 100%;
	background-color: $grey_title;
	@include r(650) {
		flex-direction: column; }

	&__left {
		position: relative;
		padding: 7px;
		padding-left: 15px;
		padding-right: 32px;
		font-family: 'Barlow Condensed', sans-serif;
		font-weight: normal;
		font-style: normal;
		color: $white_text;
		font-size: 18px;
		line-height: 1.2;
		text-transform: uppercase;
		background-color: $red;
		@include r(650) {
			display: inline-block;
			width: 100%;
			text-align: center;
			font-size: 14px; }
		&:after {
			position: absolute;
			top: 0;
			right: -39px;
			content: "";
			display: block;
			border: 17px solid transparent;
			border-left: 23px solid $red;
			@include r(965) {
				border: none; } } }

	&__right {
		padding-left: 37px;
		padding-right: 10px;
		font-style: normal;
		@include r(650) {
			display: inline-block;
			width: 100%;
			padding-left: 0;
			text-align: center;
			font-size: 10px; } } }

