.wrapper {
  width: 1000px;
  margin: 0 auto;
  margin-left: auto;
  margin-right: auto;
  @include r(1024) {
    width: 100%; } }

.container {
  width: 940px;
  margin: 0 auto;
  @include r(1024) {
    width: calc(100% - 40px); } }

.out {
  padding-top: 30px;
  padding-bottom: 50px;
  background-color: $grey_bg; }

.main {
  display: flex;
  justify-content: space-between;
  @include r(1024) {
    width: 100%; }
  @include r(950) {
    flex-wrap: wrap; }
  @include r(768) {
    flex-direction: column; } }

.main-content {
  width: 77%;
  @include r(768) {
    width: 100%; } }

.sidebar {
  width: 20%;
  @include r(768) {
    width: 100%; } }
