.mainbox {
  position: relative;
  width: 100%;
  margin-bottom: 37px;
  @include r(320) {
    width: 100%; }

  &__link {
    color: #757575;
    text-decoration: none;
    &:hover {
      .mainbox__img {
        transform: scale(1.2); } } }

  &__title {
    margin-bottom: 5px; }

  &__media {
    width: 100%;
    height: 256px;
    margin-bottom: 8px;
    overflow: hidden; }
  &__img {
    width: 100%;
    height: 100%;
    @include cover;
    transition: 1s; }

  &__discr {
    position: absolute;
    top: 148px;
    left: 40px;
    display: block;
    width: 338px;
    padding: 17px;
    padding-bottom: 20px;
    color: #e2e2e2;
    background-color: #1e1e1e;
    @include r(950) {
      position: relative;
      top: 0;
      left: 0;
      width: 100%; }
    @include r(768) {
      font-size: 10px; } } }
