.mediaNews {
  @include r(650) {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 auto; }

  &__link-title {
    text-decoration: none;
    @include r(768) {
      width: 100%; } }

  &__title {
    width: 100%;
    margin-bottom: 5px;
    padding: 5px;
    padding-left: 12px;
    padding-bottom: 8px;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: normal;
    font-style: normal;
    color: $white_text;
    font-size: 18px;
    line-height: 1.2;
    text-transform: uppercase;
    background-color: $red;
    @include r(768) {
      text-align: center;
      font-size: 14px; } }

  &__list {
    @include r(768) {
      display: flex;
      justify-content: space-between; }
    @include r(550) {
      flex-direction: column; } }

  &__link {
    color: #757575;
    text-decoration: none;
    @include r(768) {
      width: 32%; }
    @include r(550) {
      width: 100%; } }

  &__item {
    margin-bottom: 18px;
    &:hover {
      box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.3);
      .mediaNews__img {
        transform: scale(1.2); } } }

  &__media {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 172px;
    @include r(550) {
      height: 250px; } }

  &__img {
    width: 100%;
    height: 100%;
    @include cover;
    transition: 1s; }

  &__play-img {
    position: absolute;
    left: 8px;
    bottom: 8px; }


  &__item-title {
    padding: 0 4px;
    margin-top: 16px;
    margin-bottom: 6px;
    font-size: 12px;
    color: #042b38;
    font-style: normal;
    text-decoration: underline; }

  p {
    padding: 0 4px 4px 4px; }

  &__revievs {
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
    &-icon {
      display: inline-block;
      vertical-align: top;
      height: 6px;
      padding-top: 4px;
      margin-right: 5px; } } }
