.posts {

  &__item {
    margin-bottom: 30px; }

  &__title {
    margin-bottom: 27px; }

  &__content {
    display: flex;
    width: 100%;
    @include r(650) {
      flex-direction: column;
      align-items: center;
      font-size: 8px; } }

  &__img {
    width: 169px;
    height: 129px;
    margin-right: 28px; }

  &__discr {
    margin-top: 7px;
    font-size: 12px;
    font-style: normal;
    @include r(650) {
      width: 100%; } }

  &__socials {
    display: flex;
    margin-top: 20px;
    &-link {
      color: $grey_98;
      text-decoration: none; }
    @include r(320) {
      align-items: center; } }

  &__revievs {
    display: flex;
    margin-right: 20px;
    &-icon {
      padding-top: 4px;
      margin-right: 5px; } }

  &__tags {
    margin-right: 42px;

    span {
      color: $blue_text;
      margin-left: 8px;
      text-decoration: underline;
      font-style: italic; } }

  &__share {
    display: flex;
    align-items: center;
    &-link {
      width: 20px;
      height: 16px; }
    img {
      width: 16px;
      height: 16px;
      margin-right: 4px; }
    img:first-child {
      margin-left: 12px; } } }


